html {
  background-color: #000;
  height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  width: 90vw;
  height: 90vh;
  margin: 5vh auto;
  display: flex;
}

.frame-view-class {
  opacity: 1;
  grid-template-columns: 1fr;
  width: 100%;
  height: 100%;
  display: grid;
}

.content-frame {
  opacity: 0;
  border: none;
  grid-row-start: 1;
  grid-column-start: 1;
  width: 100%;
  height: 100%;
  transition: opacity 1.5s ease-in-out;
}
